import { Component, ChangeDetectionStrategy } from '@angular/core';

import { Platform } from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { SesionService } from './modulos/com/services/sesion.service'
import { EmpresaService } from './modulos/emp/services/empresa.service'
import { Empresa } from './modulos/emp/entities/empresa';
import { AuthService } from './modulos/com/services/auth.service';
import { Router } from '@angular/router';
import { AppVersion } from '@ionic-native/app-version/ngx';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html'
})
export class AppComponent {
  public appPages = [
    {
      title: 'Inicio',
      url: '/home',
      icon: 'home'
    }
  ];

  constructor(
    private appVersion: AppVersion,
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
  ) {
    this.initializeApp();
  }

  initializeApp() {
    this.platform.ready().then(() => {

      this.appVersion.getVersionNumber()
        .then(value => {
          localStorage.setItem("app_version", value);
        })
        .catch(err => {
          alert(err);
        });

      document.addEventListener("backbutton", function (e) {
        console.log("disable back button")
      }, false);

      this.statusBar.styleDefault();

      this.splashScreen.hide();
    });
  }


}
